var React = require("react");

function Note() {
  return (
    <div className="note">
      <h1>DevTown</h1>
      <p>
        We at ShapeAI (A research and education-based startup ), are a team with
        an aim to provide students with a platform to research and make projects
        in the field of AI and ML. We aim to provide an interactive learning
        platform for students where they will be able to learn and develop
        projects with our support
      </p>
    </div>
  );
}

export default Note;
